import React, { useState } from 'react';
import AgoraRTC from 'agora-rtc-sdk-ng';
import useAgora from './hooks/useAgora';
import MediaPlayer from './components/MediaPlayer';
import './Call.css';

const client = AgoraRTC.createClient({ codec: 'h264', mode: 'rtc' });

function Call() {
  const getUrlParam = (param:string) => {
    const queryString = window.location.search;
    const urlParams = new URLSearchParams(queryString);
    const paramValue = urlParams.get(param);
    return paramValue ? paramValue : "";
  };

  const [ appid, setAppid ] = useState(getUrlParam('appid'));
  const [ token, setToken ] = useState(getUrlParam('token'));
  const [ channel, setChannel ] = useState(getUrlParam('channel'));
  const {
    localVideoTrack, leave, join, changeBkg, joinState, remoteUsers
  } = useAgora(client);

  return (
    <div className='call'>
      <form className='call-form'>
        <label>
          AppID:
          <input type='text' name='appid' value={appid} onChange={(event) => { setAppid(event.target.value) }}/>
        </label>
        <label>
          Token(Optional):
          <input type='text' name='token' value={token} onChange={(event) => { setToken(event.target.value) }} />
        </label>
        <label>
          Channel:
          <input type='text' name='channel' value={channel} onChange={(event) => { setChannel(event.target.value) }} />
        </label>
        <div>
          Background:
          <select name="custom_bkg" id="custom_bkg" defaultValue='black' onChange={(event) => { changeBkg(event.target.value) }}>
            <option value="black">Black</option>
            <option value="blur">Blur</option>
            <option value="./images/V-CUBE.png">V-CUBE</option>
            <option value="./images/bg_library.jpg">Library</option>
            <option value="./images/bg_office.jpg">Office</option>
            <option value="./images/bg_beach.jpeg">Beach</option>
            <option value="./images/ems_country_cover_17.jpg">Nature</option>
          </select>
        </div>
        <div className='button-group'>
          <button id='join' type='button' className='btn btn-primary btn-sm' disabled={joinState} onClick={() => {join(appid, channel, token)}}>Join</button>
          <button id='leave' type='button' className='btn btn-primary btn-sm' disabled={!joinState} onClick={() => {leave()}}>Leave</button>
        </div>
      </form>
      <div className='player-container'>
        <div className='local-player-wrapper'>
          <p className='local-player-text'>{localVideoTrack && `localTrack`}{joinState && localVideoTrack ? `(${client.uid})` : ''}</p>
          <MediaPlayer videoTrack={localVideoTrack} audioTrack={undefined}></MediaPlayer>
        </div>
        {remoteUsers.map(user => (<div className='remote-player-wrapper' key={user.uid}>
            <p className='remote-player-text'>{`remoteVideo(${user.uid})`}</p>
            <MediaPlayer videoTrack={user.videoTrack} audioTrack={user.audioTrack}></MediaPlayer>
          </div>))}
      </div>
    </div>
  );
}

export default Call;
