import * as WasmCheck from "wasm-check";

import TfliteWasmThread from "../tflite-wasm/tflite-wasm-thread.js";
import TfliteWasm from "../tflite-wasm/tflite-wasm.js";
import TfliteSimd from "../tflite-wasm/tflite-wasm-simd.js";

export default class TfLiteWasm {
  static getWidth(isLite: boolean = false): number {
    return isLite ? 160 : 256;
  }
  static getHeight(isLite: boolean = false): number {
    return isLite ? 96 : 144;
  }
  protected static getModelId(isLite: boolean = false) {
    switch (TfLiteWasm.getWidth(isLite)) {
      case 192:
        return 0;
      case 128:
        return 1;
      case 256:
        return 2;
      case 160:
        return 3;
    }
  }
  protected offset = 0;
  protected runInferenceWasm: ((offset: number, size: number) => void) | null = null;
  protected loadModel: ((modelId: number, threads: number) => number) | null = null;
  protected instance: any;
  protected width = TfLiteWasm.getWidth();
  protected height = TfLiteWasm.getHeight();
  protected readonly threads: number;
  initDone: Promise<void>;

  protected tfLiteWasmInit = (instance: any, initResolve: () => void) => {
    this.instance = instance;
    // Allocate enough space for pixel data in the wasm linear memory
    // and get a pointer to the start of the data

    this.offset = instance._malloc(this.width * this.height * 4);
    this.runInferenceWasm = instance.cwrap("run_inference", "void", ["number", "number"]);
    this.loadModel = instance.cwrap("load_model", "int", ["number", "number"]);

    const allInitDone = () => {
      this.loadModel!(TfLiteWasm.getModelId(), this.threads);
      initResolve();
    };

    if (instance.PThread && this.threads > 1) {
      for (let i = 0; i < this.threads - 1; i++) {
        instance.PThread.allocateUnusedWorker();
      }
      let numWorkersToLoad = instance.PThread.unusedWorkers.length;
      instance.PThread.unusedWorkers.forEach(function (w: any) {
        instance.PThread.loadWasmModuleToWorker(w, function () {
          if (!--numWorkersToLoad) allInitDone();
        });
      });
    } else {
      allInitDone();
    }
  };

  constructor(threads: number, locateFile: (path: string, scriptDirectory: string) => string) {
    this.threads = threads;
    this.initDone = new Promise((resolved) => {
      try {
        if (WasmCheck.feature.simd) {
          TfliteSimd({
            mainScriptUrlOrBlob: "./tflite-wasm-simd.js",
            locateFile,
          }).then((instance: any) => this.tfLiteWasmInit(instance, resolved));
        } else {
          throw new Error("NO Wasm SIMD feature");
        }
      } catch (e) {
        try {
          if (WasmCheck.feature.threads && typeof SharedArrayBuffer === "function" && threads > 1) {
            TfliteWasmThread({
              mainScriptUrlOrBlob: "./tflite-wasm-thread.js",
              locateFile,
            }).then((instance: any) => this.tfLiteWasmInit(instance, resolved));
          } else {
            throw new Error("NO Wasm threads feature or threads is not requested");
          }
        } catch (e) {
          TfliteWasm({
            mainScriptUrlOrBlob: "./tflite-wasm.js",
            locateFile,
          }).then((instance: any) => this.tfLiteWasmInit(instance, resolved));
        }
      }
    });
  }

  isInitDone() {
    return this.loadModel != null && this.runInference != null;
  }

  runInference(imageData: ImageData) {
    if (
      (imageData.width !== TfLiteWasm.getWidth() || imageData.height !== TfLiteWasm.getHeight()) &&
      (imageData.width !== TfLiteWasm.getWidth(true) ||
        imageData.height !== TfLiteWasm.getHeight(true))
    ) {
      throw new Error(
        "TfLiteWasm process imageData must be " +
          TfLiteWasm.getWidth() +
          "x" +
          TfLiteWasm.getHeight() +
          " or " +
          TfLiteWasm.getWidth(true) +
          "x" +
          TfLiteWasm.getHeight(true),
      );
    }
    if (this.width !== imageData.width || this.height !== imageData.height) {
      this.width = imageData.width;
      this.height = imageData.height;
      this.loadModel!(
        TfLiteWasm.getModelId(this.width === TfLiteWasm.getWidth(true)),
        this.threads,
      );
    }
    this.instance.HEAPU8.set(imageData.data, this.offset);
    this.runInferenceWasm!(this.offset, this.width * this.height * 4);
    return this.instance.HEAPU8.slice(this.offset, this.offset + this.width * this.height * 4);
  }
}
