// eslint-disable-next-line import/no-webpack-loader-syntax
import TfLiteWorker from "worker-loader!./TfLiteWorker.js";

import TfLiteWasm from "./TfLiteWasm";
import WebGLImageProcessing from "./WebGLImageProcessing";

class TfLiteJob {
  imageData?: ImageData;
  readonly inputVideo: HTMLCanvasElement | ImageData;
  readonly eventID: number;
  //readonly timeStart: number;
  constructor(inputVideo: HTMLCanvasElement | ImageData, eventID: number) {
    this.inputVideo = inputVideo;
    this.eventID = eventID;
    //this.timeStart = performance.now();
  }
}

export default class TfLite {
  private static instance?: TfLite | null;

  protected tfLiteWasm: TfLiteWasm | null = null;
  protected tfLiteWorkers: TfLiteWorker[] = [];
  protected curWorker = 0;
  protected tfLiteJobs: TfLiteJob[] = [];
  protected webGLImageProcessing: WebGLImageProcessing;
  protected inputCanvas: HTMLCanvasElement;
  protected inputCanvasScaled?: HTMLCanvasElement;

  public blurBackground = false;
  public readonly canvas: HTMLCanvasElement;
  public static readonly TRANSPARENT: ImageData = new ImageData(
    new Uint8ClampedArray([0, 0, 0, 0]),
    1,
    1,
  );
  public onInferenceResult?: () => void;

  public static locateFile = (path: string, scriptDirectory: string) => {
    // eslint-disable-next-line no-restricted-globals
    const locationHref = location.href;
    const staticJs = "/static/js/";
    const staticJsIdx = locationHref.indexOf(staticJs);
    if (staticJsIdx >= 0) {
      return locationHref.substring(0, staticJsIdx) + "/tflite-wasm/" + path;
    }
    return "./tflite-wasm/" + path;
  };
  public static threads = Math.min(2, navigator.hardwareConcurrency);

  private constructor(numWorkers = 0) {
    for (let i = 0; i < numWorkers; i++) {
      this.tfLiteWorkers.push(new TfLiteWorker());
      this.tfLiteWorkers[i].onmessage = this.onMessage;
      this.tfLiteWorkers[i].postMessage({ eventType: "INITIALISE" });
    }

    if (numWorkers <= 0) {
      if (TfLite.threads > navigator.hardwareConcurrency) {
        console.warn("requested TfLite.threads > navigator.hardwareConcurrency");
      }
      this.tfLiteWasm = new TfLiteWasm(TfLite.threads, TfLite.locateFile);
    }
    this.canvas = document.createElement("canvas");
    this.inputCanvas = document.createElement("canvas");

    const gl = this.canvas.getContext("webgl");
    if (gl == null) {
      throw new Error("TfLite canvas.getContext(webgl) fail");
    }
    this.webGLImageProcessing = new WebGLImageProcessing(
      gl,
      TfLiteWasm.getWidth(),
      TfLiteWasm.getHeight(),
    );
    // set transparent background
    this.setBackground(TfLite.TRANSPARENT);
  }

  destroy() {
    this.webGLImageProcessing.destroy();
    for (const t of this.tfLiteWorkers) {
      t.terminate();
    }
    this.tfLiteWorkers = [];
    delete TfLite.instance;
  }

  static getInstance() {
    if (TfLite.instance === undefined) {
      try {
        TfLite.instance = new TfLite();
      } catch (e) {
        console.error("Failed to initialise TfLite");
        console.error(e);
        TfLite.instance = null;
      }
    }
    return TfLite.instance;
  }

  protected onMessage = (event: MessageEvent) => {
    const { eventType, eventData, eventID } = event.data;
    if (eventType === "INFERENCE_RESULT") {
      let tfliteJob = null;
      for (const iJob of this.tfLiteJobs) {
        if (!iJob.imageData && iJob.eventID === eventID) {
          tfliteJob = iJob;
          break;
        }
      }
      tfliteJob!.imageData = new ImageData(
        new Uint8ClampedArray(eventData[0]),
        eventData[1],
        eventData[2],
      );
      this.onInferenceResult?.();
    } else if (eventType === "ERROR") {
      console.error(event);
    }
  };

  isBusy(): boolean {
    return this.tfLiteJobs.length > 1 && this.tfLiteJobs.length > this.tfLiteWorkers.length * 2;
  }

  render() {
    let tfliteJob = null;
    if (this.tfLiteJobs[0]?.imageData) {
      tfliteJob = this.tfLiteJobs.shift();
    }

    if (!tfliteJob) {
      //console.log("oh oh nothing to render");
      return false;
    }
    //console.log("time delay:" + (performance.now() - tfliteJob!.timeStart));

    this.webGLImageProcessing.updateTexture(
      WebGLImageProcessing.Textures.VIDEO_MASK,
      tfliteJob.imageData,
    );
    this.webGLImageProcessing.updateTexture(
      WebGLImageProcessing.Textures.VIDEO_INPUT,
      tfliteJob.inputVideo,
    );

    if (this.blurBackground) {
      this.webGLImageProcessing.blurBackground(WebGLImageProcessing.Textures.VIDEO_MASK);
    } else {
      this.webGLImageProcessing.filterMask(WebGLImageProcessing.Textures.VIDEO_MASK);
    }
    this.webGLImageProcessing.render();
    return true;
  }

  //setBackground, input can be image, video, imagedata
  setBackground(input: ImageData | HTMLImageElement | HTMLVideoElement) {
    this.webGLImageProcessing.updateTexture(WebGLImageProcessing.Textures.BACKGROUND, input);
  }

  process(
    input: HTMLVideoElement,
    isLite: boolean,
    targetWidth: number,
    targetHeight: number,
    yOnTop = false,
  ) {
    if (this.isBusy()) {
      //console.log("oh oh the queue is too long, can't quueue anymore");
      return;
    }

    const inputWidth = input.videoWidth | input.width;
    const inputHeight = input.videoHeight | input.height;
    this.inputCanvas.width = inputWidth;
    this.inputCanvas.height = inputHeight;
    const inputCanvasCtx = this.inputCanvas.getContext("2d");
    if (!inputCanvasCtx) {
      throw new Error("TfLite.inputCanvas.getContext(2d) fail");
    }

    inputCanvasCtx.drawImage(input, 0, 0, inputWidth, inputHeight, 0, 0, inputWidth, inputHeight);
    // Get the image data from the hidden canvas
    const inputImageData = inputCanvasCtx.getImageData(0, 0, inputWidth, inputHeight);

    if (this.canvas.width !== targetWidth || this.canvas.height !== targetHeight) {
      this.canvas.width = targetWidth;
      this.canvas.height = targetHeight;
    }

    //pixi.js filter v0 coordinate is on top, so we set viewport accordingly
    this.webGLImageProcessing.setViewPort(
      0,
      yOnTop ? this.canvas.height - targetHeight : 0,
      targetWidth,
      targetHeight,
    );

    const width = TfLiteWasm.getWidth(isLite);
    const height = TfLiteWasm.getHeight(isLite);

    if (this.tfLiteWasm) {
      if (!this.tfLiteWasm.isInitDone()) {
        return;
      }
      if (!this.inputCanvasScaled) {
        this.inputCanvasScaled = document.createElement("canvas");
      }
      this.inputCanvasScaled.width = width;
      this.inputCanvasScaled.height = height;
      const ctx = this.inputCanvasScaled.getContext("2d");
      if (!ctx) {
        throw new Error("TfLite.inputCanvasScaled.getContext(2d) fail");
      }
      // Draw video frame to  canvas
      ctx.drawImage(this.inputCanvas, 0, 0, inputWidth, inputHeight, 0, 0, width, height);
      // Get the image data from the hidden canvas
      const imageData = ctx.getImageData(0, 0, width, height);
      const job = new TfLiteJob(inputImageData, this.curWorker);
      const clampedArray = new Uint8ClampedArray(this.tfLiteWasm.runInference(imageData));
      job.imageData = new ImageData(clampedArray, width, height);
      this.tfLiteJobs.push(job);
    } else {
      this.tfLiteJobs.push(new TfLiteJob(inputImageData, this.curWorker));

      if (typeof OffscreenCanvas === "undefined") {
        if (!this.inputCanvasScaled) {
          this.inputCanvasScaled = document.createElement("canvas");
        }
        this.inputCanvasScaled.width = width;
        this.inputCanvasScaled.height = height;
        const ctx = this.inputCanvasScaled.getContext("2d");
        if (!ctx) {
          throw new Error("TfLite.inputCanvasScaled.getContext(2d) fail");
        }
        // Draw video frame to  canvas
        ctx.drawImage(this.inputCanvas, 0, 0, inputWidth, inputHeight, 0, 0, width, height);
        // Get the image data from the hidden canvas
        const imageData = ctx.getImageData(0, 0, width, height);
        this.tfLiteWorkers[this.curWorker].postMessage(
          {
            eventType: "CALL",
            eventData: {
              method: "RUN_INFERENCE",
              arguments: [imageData.data.buffer, width, height, width, height],
            },
            eventID: this.curWorker,
          } /*,[imageData.data.buffer]*/,
        );
      } else {
        this.tfLiteWorkers[this.curWorker].postMessage(
          {
            eventType: "CALL",
            eventData: {
              method: "RUN_INFERENCE",
              arguments: [inputImageData.data.buffer, width, height, inputWidth, inputHeight],
            },
            eventID: this.curWorker,
          } /*,[inputImageData.data.buffer]*/,
        );
      }
    }
    this.curWorker = (this.curWorker + 1) % this.tfLiteWorkers.length;
  }
}
