import { useState, useEffect, useRef, useCallback } from 'react';
import AgoraRTC, {
  IAgoraRTCClient, IAgoraRTCRemoteUser, MicrophoneAudioTrackInitConfig, CameraVideoTrackInitConfig, IMicrophoneAudioTrack, ILocalVideoTrack, ILocalAudioTrack } from 'agora-rtc-sdk-ng';
import {TfLite} from 'vcube-virtual-background';

interface CanvasElement extends HTMLCanvasElement {
  captureStream(frameRate?: number): MediaStream;
}

export default function useAgora(client: IAgoraRTCClient | undefined)
  :
   {
      localAudioTrack: ILocalAudioTrack | undefined,
      localVideoTrack: ILocalVideoTrack | undefined,
      joinState: boolean,
      leave: Function,
      join: Function,
      changeBkg: Function,
      remoteUsers: IAgoraRTCRemoteUser[],
    }
    {
  const [localVideoTrack, setLocalVideoTrack] = useState<ILocalVideoTrack | undefined>(undefined);
  const [localAudioTrack, setLocalAudioTrack] = useState<ILocalAudioTrack | undefined>(undefined);
  
  const localVideo = useRef<HTMLVideoElement>(document.createElement('video'));
  const tflite = useRef<TfLite>(TfLite.getInstance());
  const looper = useRef<number>();

  const [joinState, setJoinState] = useState(false);

  const [remoteUsers, setRemoteUsers] = useState<IAgoraRTCRemoteUser[]>([]);
  const loop = useCallback((delay) => {
    const startTime = performance.now();
    tflite.current!.process(localVideo.current, false, localVideo.current.videoWidth, localVideo.current.videoHeight);
    tflite.current!.render();
    const dt = performance.now() - startTime;
    looper.current = window.setTimeout(loop, Math.max(0, delay - dt), delay);
  }, []);

  async function createLocalTracks(audioConfig?: MicrophoneAudioTrackInitConfig, videoConfig?: CameraVideoTrackInitConfig)
  : Promise<[IMicrophoneAudioTrack, ILocalVideoTrack]> {
    const microphoneTrack = await AgoraRTC.createMicrophoneAudioTrack(audioConfig);
    setLocalAudioTrack(microphoneTrack);
    const fps = 15;
    const customVideoStream = await navigator.mediaDevices.getUserMedia({
      video : {
        frameRate:fps,
        width:640,
        height:360
      }
    });
    localVideo.current.autoplay = true;
    localVideo.current.onloadedmetadata = () => {
      tflite.current!.canvas.width = localVideo.current.videoWidth;
      tflite.current!.canvas.height = localVideo.current.videoHeight;
      loop(1000/fps);
    }
    localVideo.current.srcObject = customVideoStream;

    const canvasStream = (tflite.current!.canvas as CanvasElement).captureStream();
    const cameraTrack = await AgoraRTC.createCustomVideoTrack({mediaStreamTrack: canvasStream.getVideoTracks()[0]});
    setLocalVideoTrack(cameraTrack);
    return [microphoneTrack, cameraTrack];
  }
  
  function changeBkg(bkg:string) {
    tflite.current!.blurBackground = bkg === 'blur';
    if (bkg === 'blur'){
      return;
    }

    if (bkg === 'black') {
      tflite.current!.setBackground(TfLite.TRANSPARENT);
    } else {
      tflite.current!.blurBackground = false;
      const image = new Image();
      image.onload = function () {
        tflite.current!.setBackground(image);
      };
      image.src = bkg;
    }
  }

  async function join(appid: string, channel: string, token?: string, uid?: string | number | null) {
    if (!client) return;
    const [microphoneTrack, cameraTrack] = await createLocalTracks();
    
    await client.join(appid, channel, token || null);
    await client.publish([microphoneTrack, cameraTrack]);

    (window as any).client = client;
    (window as any).videoTrack = cameraTrack;

    setJoinState(true);
  }

  async function leave() {
    clearTimeout(looper.current);
    if (localAudioTrack) {
      localAudioTrack.stop();
      localAudioTrack.close();
    }
    if (localVideoTrack) {
      localVideoTrack.stop();
      localVideoTrack.close();
    }
    if (localVideo.current) {
      const mediaStream = localVideo.current.srcObject as MediaStream;
      mediaStream.getTracks().forEach(function(track) {
        track.stop();
      });
    }
    setRemoteUsers([]);
    setJoinState(false);
    await client?.leave();
  }

  useEffect(() => {
    if (!client) return;
    setRemoteUsers(client.remoteUsers);

    const handleUserPublished = async (user: IAgoraRTCRemoteUser, mediaType: 'audio' | 'video') => {
      await client.subscribe(user, mediaType);
      // toggle rerender while state of remoteUsers changed.
      setRemoteUsers(remoteUsers => Array.from(client.remoteUsers));
    }
    const handleUserUnpublished = (user: IAgoraRTCRemoteUser) => {
      setRemoteUsers(remoteUsers => Array.from(client.remoteUsers));
    }
    const handleUserJoined = (user: IAgoraRTCRemoteUser) => {
      setRemoteUsers(remoteUsers => Array.from(client.remoteUsers));
    }
    const handleUserLeft = (user: IAgoraRTCRemoteUser) => {
      setRemoteUsers(remoteUsers => Array.from(client.remoteUsers));
    }
    client.on('user-published', handleUserPublished);
    client.on('user-unpublished', handleUserUnpublished);
    client.on('user-joined', handleUserJoined);
    client.on('user-left', handleUserLeft);

    return () => {
      client.off('user-published', handleUserPublished);
      client.off('user-unpublished', handleUserUnpublished);
      client.off('user-joined', handleUserJoined);
      client.off('user-left', handleUserLeft);
    };
  }, [client]);

  return {
    localAudioTrack,
    localVideoTrack,
    joinState,
    leave,
    join,
    changeBkg,
    remoteUsers,
  };
}